<template>
  <div>
    <el-form ref="new_form" :model="new_form" :rules="rules" label-width="120px" class="new-box">
      <h3 class="new-title">注册账号</h3>
      <el-form-item label="请输入邮箱" prop="email">
        <el-input v-model="new_form.email"></el-input>
      </el-form-item>
      <el-form-item label="请输入验证码" prop="verify">
        <el-input v-model="new_form.verify" style="width: 65%;"></el-input>
        <el-button round style="font-size: small;" @click="sendVerify">发送验证码</el-button>
      </el-form-item>
      <el-form-item label="账号" prop="name">
        <el-input v-model="new_form.name"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password1">
        <el-input v-model="new_form.password1" show-password></el-input>
      </el-form-item>
      <el-form-item label="确认密码" prop="password2">
        <el-input v-model="new_form.password2" show-password></el-input>
      </el-form-item>
      <el-form-item class="new-button">
        <el-button type="primary" @click="submitForm('new_form')">注册</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    name: "newuser",
    data() {
      return {
        new_form: {
          email: '',
          verify: '',
          name: '',
          password1: '',
          password2: ''
        },
        rules: {
          email: [{
            required: true,
            message: '请输入邮箱'
          }],
          verify: [{
            required: true,
            message: '请输入验证码'
          }],
          name: [{
            required: true,
            message: '请输入账号'
          }],
          password1: [{
            required: true,
            message: '请输入密码'
          }],
          password2: [{
            required: true,
            message: '请确认密码'
          }]
        }
      }
    },
    methods: {
      sendVerify() {
        if (this.new_form.email == '') {
          this.$alert('请输入邮箱！', '提示', {
            confirmButtonText: '确定'
          });
        } else {
          this.$axios.post('http://landx.top:9002/sendVerify', {
              email: this.new_form.email
            })
            .then(response => {
              console.log(response.data.msg);
              if (response.data.msg == 'SUCCESS') {
                this.$alert('验证码发送成功！', '提示', {
                  confirmButtonText: '确定'
                });
              } else {
                this.$alert('邮箱输入不正确！', '提示', {
                  confirmButtonText: '确定'
                });
              }
            })
        }
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if (this.new_form.password1 == this.new_form.password2) {
              this.$axios.post('http://landx.top:9002/registe', {
                email:this.new_form.email,
                  name: this.new_form.name,
                  password: this.new_form.password1,
                  vnum:this.new_form.verify
                })
                .then(response => {
                  console.log(response.data.msg)
                  if(response.data.msg=="SUCCESS"){
                    this.$alert('注册成功，快去登陆吧~', '提示', {
                      confirmButtonText: '确定'
                    })
                  }
                  else if(response.data.msg=="用户名已被注册"){
                    this.$alert('用户名已被注册过啦，重新想一个吧~', '提示', {
                      confirmButtonText: '确定'
                    })
                  }
                  else if(response.data.msg=="邮箱已被注册"){
                    this.$alert('邮箱已被注册过啦，换一个邮箱吧~', '提示', {
                      confirmButtonText: '确定'
                    })
                  }
                  else if(response.data.msg=="验证码错误"){
                    this.$alert('验证码错误！', '提示', {
                      confirmButtonText: '确定'
                    })
                  }
                  else{
                    this.$alert('服务器开小差啦~', '提示', {
                      confirmButtonText: '确定'
                    })
                  }
                })
            } else {
              this.$alert('两次密码输入不一致！', '提示', {
                confirmButtonText: '确定'
              })
            }
          } else {
            this.$alert('请输入相关信息！', '提示', {
              confirmButtonText: '确定'
            })
            return false;
          }
        })
      }
    }
  }
</script>

<style>
  .new-box {
    width: 500px;
    margin: 200px auto;
    border: 1px solid #DCDFE6;
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0 0 30px #DCDFE6;
  }

  .new-title {
    text-align: center;
  }

  .new-button {
    text-align: center;
  }
</style>



