<template>
  <div>
    <table>
      <div v-for="(search_info,index) in search_infos" :key="index" v-if="index%4==0">
        <tr>
          <td class="box">
             <el-card >
               <div >
                 <div style="float: right;">
                   <img v-bind:src=search_info.picture style="max-width: 100px;height: auto;">
                 </div>
                 <div>
                   <h4>{{search_info.user_name}}</h4>
                   <p><span>{{search_info.category}} | </span><span class="no-bd">{{search_info.name}}</span>
                   <p>拾取时间：{{search_info.time}}</p>
                   <p>联系方式：{{search_info.contact_info}}</p>
                   <p>描述：{{search_info.discrip}}</p>
                 </div>
               </div>
             </el-card>
          </td>
          <td v-if="index+1<search_infos.length" class="box">
             <el-card >
               <div  >
                 <div style="float: right; ">
                   <img v-bind:src=search_infos[index+1].picture style="max-width: 100px;height: auto;" />
                 </div>
                 <div>
                   <h4>{{search_infos[index+1].user_name}}</h4>
                   <p><span>{{search_infos[index+1].category}} | </span><span class="no-bd">{{search_info.name}}</span>
                   <p>拾取时间：{{search_infos[index+1].time}}</p>
                   <p>联系方式：{{search_infos[index+1].contact_info}}</p>
                   <p>描述：{{search_infos[index+1].discrip}}</p>
                 </div>
               </div>
             </el-card>
          </td>
          <td v-if="index+2<search_infos.length" class="box">
             <el-card >
               <div  >
                 <div style="float: right; ">
                   <img v-bind:src=search_infos[index+2].picture style="max-width: 100px;height: auto;" />
                 </div>
                 <div >
                   <h4>{{search_infos[index+2].user_name}}</h4>
                   <p><span>{{search_infos[index+2].category}} | </span><span class="no-bd">{{search_info.name}}</span>
                   <p>拾取时间：{{search_infos[index+2].time}}</p>
                   <p>联系方式：{{search_infos[index+2].contact_info}}</p>
                   <p>描述：{{search_infos[index+2].discrip}}</p>
                 </div>
               </div>
             </el-card>
          </td>
          <td v-if="index+3<search_infos.length" class="box">
           <el-card >
             <div >
               <div style="float: right; ">
                 <img v-bind:src=search_infos[index+3].picture style="max-width: 100px;height: auto;" />
               </div>
               <div>
                 <h4>{{search_infos[index+3].user_name}}</h4>
                 <p><span>{{search_infos[index+3].category}} | </span><span class="no-bd">{{search_info.name}}</span>
                 <p>拾取时间：{{search_infos[index+3].time}}</p>
                 <p>联系方式：{{search_infos[index+3].contact_info}}</p>
                 <p>描述：{{search_infos[index+3].discrip}}</p>
               </div>
             </div>
           </el-card>
          </td>
        </tr>
      </div>
    </table>
  </div>
</template>

<script>
  export default {
    name: "search_info",
    data() {
      return {
        search_infos: [{
          id: '',
          name: '',
          discrip: '',
          category: '',
          path: [
            '',
            ''
          ],
          time: '',
          if_find: '',
          user_name: '',
          contact_info: '',
          picture: '',
          type: ''
        }]
      }
    },
    mounted() {
      this.search_infos = JSON.parse(this.$route.params.searchInfos);
      console.log(this.search_infos);
    }
  }
</script>

<style>
  #box {
    width: 100%;
    height: 400px;
    margin: auto;
    padding: 10px;
    border: 1px solid #DCDFE6;
    border-radius: 10px;
    box-shadow: 0 0 30px #DCDFE6;
  }
</style>



