<template>
  <div>
    <el-form ref="search_box" :model="form" label-width="150px" class="search_box">
      <el-form-item label="事件类型">
        <el-radio-group v-model="form.type">
          <el-radio label="1">失物招领事件</el-radio>
          <el-radio label="0">遗失物事件</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="物品名称">
        <el-input v-model="form.name" style="width: 30%;"></el-input>
      </el-form-item>
      <el-form-item label="物品类型">
        <el-select v-model="form.category" placeholder="请选择物品类型">
          <el-option label="生活用品" value="生活"></el-option>
          <el-option label="数码产品" value="数码"></el-option>
          <el-option label="美妆产品" value="美妆"></el-option>
          <el-option label="证件" value="证件"></el-option>
          <el-option label="钥匙" value="钥匙"></el-option>
          <el-option label="其他" value="其他"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="时间范围">
        <el-date-picker v-model="form.time" type="datetimerange" range-separator="至" start-placeholder="开始日期"
          end-placeholder="结束日期" value-format="yyyy-MM-dd HH:mm:ss">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="请绘制搜索范围:">
        <iframe src="../../static/map1.html" width="800px" height="530px" frameborder="0"></iframe>
      </el-form-item>
      <el-form-item class="search_button">
        <el-button @click="back">取消</el-button>
        <el-button @click="onSubmit" type="primary">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    name: "search",
    data() {
      return {
        publicPath: process.env.BASE_URL,
        form: {
          name: '',
          type: '1',
          if_find: '0',
          date: '',
          category: '',
          path: [],
          time: [new Date(2021, 6, 1, 10, 10), new Date(2021, 6, 2, 10, 10)],
        },
        searchInfos: []
      }
    },
    mounted() {
      window.addEventListener("message", this.handleMessage);
    },
    methods: {
      onSubmit() {
        if(this.form.time==null){
          this.form.time=['',''];
        }
        console.log(this.form.time);
        this.$axios.post('http://landx.top:9002/searchInfo', {
            type: this.form.type,
            if_find: this.form.if_find,
            name: this.form.name,
            start_time: this.form.time[0],
            end_time:this.form.time[1],
            category: this.form.category,
            path: this.form.path
          })
          .then(response => {
            if (response.data.msg == 'SUCCESS' && response.data.data.length > 0) {
              // console.log(response.data);
              this.searchInfos = response.data.data;
              console.log(this.searchInfos);
              this.$router.push({
                name: 'search_info',
                params: {
                  searchInfos: JSON.stringify(this.searchInfos)
                }
              });
            } else if (response.data.data.length == 0) {
              this.$alert('什么也没有搜索到~', '提示', {
                confirmButtonText: '确定'
              });
            } else {
              this.$alert('服务器开小差啦~', '提示', {
                confirmButtonText: '确定'
              });
            }

          });
      },
      back() {
        this.$router.go(-1);
      },
      handleMessage(event) {
        const data = event.data;
        console.log(data);
        this.form.path.length = 0;
        for (var i in data.params.res) {
          var item = data.params.res[i];
          var tmp = [];
          tmp.push(item.lng);
          tmp.push(item.lat);
          this.form.path.push(tmp);
        }
        console.log(this.form.path);
      },
      getSTime(val) {
        this.sTime = val; //这个sTime是在data中声明的，也就是v-model绑定的值
      }
    }
  }
</script>

<style>
  .search_box {
    width: 1000px;
    margin: auto;
    border: 1px solid #DCDFE6;
    border-radius: 10px;
    box-shadow: 0 0 30px #DCDFE6;
  }

  .search_button {
    text-align: center;
  }
</style>



