<template>
  <div>
      <div v-for="(find_info,index) in find_infos" :key="index" class='box'>
        <transition name="slide-fade">
        <el-card v-if="show">
          <div>
            <div style="float: right; ">
              <img v-bind:src=find_info.picture style="max-width: 100px;height: auto;" />
            </div>
            <div style="float: left;">
              <h4>{{find_info.user_name}}</h4>
              <p><span>{{find_info.category}} | </span><span class="no-bd">{{find_info.name}}</span>
              <p>拾取时间：{{find_info.time}}</p>
              <p>联系方式：{{find_info.contact_info}}</p>
              <p>描述：{{find_info.discrip}}</p>
            </div>
          </div>
        </el-card>
        </transition>
      </div>

  </div>
</template>

<script>
  import {
    EventBus
  } from '../util/event-bus.js'
  export default {
    name: "findinfo",
    data() {
      return {
        show:false,
        find_infos: [{
          id: '',
          name: '',
          discrip: '',
          category: '',
          path: [],
          time: '',
          if_find: '',
          user_name: '',
          contact_info: '',
          picture: '',
          type: ''
        }]
      }
    },
    mounted() {
      this.$axios.get('http://landx.top:9002/findInfo').then(
        response => {
          this.show=false
          this.find_infos = response.data.data;
          for (var i in this.find_infos) {
            if (this.find_infos[i].picture == null) {
              this.find_infos[i].picture = '../assets/' + this.find_infos[i].category + '.png';
            }
          }
          this.show=true
        }
      )
    },
    props: ['locate'],
    methods: {
      load() {
        this.count += 2
      },
    }
  }
</script>

<style>
  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 480px;
    background-color: beige;
  }

  .box {
    width: 400px;
    margin: auto;
    padding: 10px;
    border: 1px solid #DCDFE6;
    border-radius: 10px;
    box-shadow: 0 0 30px #DCDFE6;
  }

  /* 可以设置不同的进入和离开动画 */
  /* 设置持续时间和动画函数 */
  .slide-fade-enter-active {
    transition: all .3s ease;
  }

  .slide-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }

  .slide-fade-enter,
  .slide-fade-leave-to

  /* .slide-fade-leave-active for below version 2.1.8 */
    {
    transform: translateX(10px);
    opacity: 0;
  }
</style>



