<template>
  <div>
    <el-form ref="login_form" :model="login_form" :rules="rules" label-width="80px" class="login-box">
      <h3 class="login-title">欢迎来到北京大学失物招领系统</h3>
      <el-form-item label="账号" prop="name">
          <el-input v-model="login_form.name"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
          <el-input v-model="login_form.password" show-password></el-input>
          <router-link to='/newuser' style="float: right;">注册账号</router-link>
      </el-form-item>
      <el-form-item class="login-button">
          <el-button type="primary" @click="submitForm('login_form')">登陆</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script src="https://unpkg.com/axios/dist/axios.min.js"></script>
<script>
  export default{
    name:'Login',
    data(){
      return{
        login_form:{
          name:'',
          password:''
        },
        rules: {
          name: [
            { required: true, message: '请输入账号'}
          ],
          password: [
            { required: true, message: '请输入密码'}
          ]
        }
      }
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$axios.post('http://landx.top:9002/login',{
              name:this.login_form.name,
              password:this.login_form.password
            })
            .then(response=>{
              if(response.data.msg=='SUCCESS'){
                this.$root.user_name=this.login_form.name;
                this.$root.token=response.data.token;
                this.$root.hasEnter=true;
                this.$router.push('/map');
                sessionStorage.setItem('user_name',this.$root.user_name);
                sessionStorage.setItem('token',this.$root.token);
                sessionStorage.setItem('hasEnter','1');
              }
              else if(response.data.msg=='FAILED'){
                this.$alert('系统开小差啦~', '提示', {
                  confirmButtonText: '确定'
                });
              }
              else if(response.data.msg=='WRONG PASSWORD'){
                this.$alert('密码输入错误！', '提示', {
                  confirmButtonText: '确定'
                });
              }
              else if(response.data.msg=='NO SUCH USER'){
                this.$alert('用户不存在！', '提示', {
                  confirmButtonText: '确定'
                });
              }
            })
        }
        else {
          this.$alert('请输入账号密码！', '提示', {
            confirmButtonText: '确定'
            })
            return false;
          }
        })
      }
    }
  }

</script>

<style scoped>
  .login-box{
    width: 400px;
    margin: 200px auto;
    border: 1px solid #DCDFE6;
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0 0 30px #DCDFE6;
  }
  .login-title{
    text-align: center;
  }
  .login-button{
    text-align: center;
  }
</style>



