<template>
  <div>
    <el-form ref="add_form" :model="info" label-width="200px" :rules="rules" class="add_box">
      <el-form-item label="事件类型">
        <el-radio-group v-model="info.type">
          <el-radio label="1" @change="change_map">失物招领事件</el-radio>
          <el-radio label="0" @change="change_map">遗失物事件</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="物品名称" prop="name">
        <el-input v-model="info.name" style="width: 30%;"></el-input>
      </el-form-item>
      <el-form-item label="物品类型" prop="category">
        <el-select v-model="info.category" placeholder="请选择物品类型">
          <el-option label="生活用品" value="生活"></el-option>
          <el-option label="数码产品" value="数码"></el-option>
          <el-option label="美妆产品" value="美妆"></el-option>
          <el-option label="证件" value="证件"></el-option>
          <el-option label="钥匙" value="钥匙"></el-option>
          <el-option label="其他" value="其他"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="拾取/遗失时间" prop="time">
        <el-col :span="8">
          <el-date-picker type="date" placeholder="选择日期" v-model="info.date" style="width: 50%;" @change="getSTime"
            value-format="yyyy-MM-dd"></el-date-picker>
        </el-col>
        <el-col :span="8">
          <el-time-picker placeholder="选择时间" v-model="info.time" style="width: 50%;" @change="getSTime"
            value-format="HH:mm:ss"></el-time-picker>
        </el-col>
      </el-form-item>
      <el-form-item label="事件描述:" style="width: 80%;">
        <el-input type="textarea" v-model="info.discrip"></el-input>
      </el-form-item>
      <el-form-item label="联系方式" prop="contact_info">
        <el-input v-model="info.contact_info" style="width: 30%;"></el-input>
      </el-form-item>
      <el-form-item label="请选择拾取地点或绘制路线:">
        <iframe v-bind:hidden="hide_sign1" src="../../static/map2.html" width="650px" height="530px"
          frameborder="0"></iframe>
        <iframe v-bind:hidden="hide_sign2" src="../../static/map3.html" width="650px" height="530px"
          frameborder="0"></iframe>
      </el-form-item>
      <el-form-item label="上传图片">
        <input id="inputElement" name="file" type="file" accept="image/png,image/jpeg" />
      </el-form-item>
      <el-form-item class="add_button">
        <el-button @click="back">取消</el-button>
        <el-button @click="onSubmit('add_form')" type="primary">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    name: "addinfo",
    data() {
      return {
        info: {
          id: '',
          name: '',
          discrip: '',
          category: '',
          path: [],
          date: '',
          time: '',
          if_find: '0',
          user_name: '',
          contact_info: '',
          picture: '',
          type: '1'
        },
        hide_sign1: true,
        hide_sign2: false,
        rules: {
          name: [{
            required: true,
            message: '请输入物品名称'
          }],
          category: [{
            required: true,
            message: '请选择物品类别'
          }],
          time: [{
            required: true,
            message: '请选择拾取/遗失时间'
          }],
          contact_info: [{
            required: true,
            message: '请输入联系方式'
          }]
        }
      }
    },
    mounted() {
      window.addEventListener("message", this.handleMessage);
      if(sessionStorage.getItem('hasEnter')=='1'){
        this.$root.user_name=sessionStorage.getItem('user_name');
        this.$root.token=sessionStorage.getItem('token');
        this.$root.hasEnter=true;
      }
    },
    methods: {
      change_map() {
        this.info.path.length=0;
        console.log(this.info.type)
        if (this.info.type == '1') {
          this.hide_sign1 = true;
          this.hide_sign2 = false;
        } else {
          this.hide_sign1 = false;
          this.hide_sign2 = true;
        }
      },
      onSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if (this.info.path.length > 0) {
              if (this.$root.hasEnter) {
                let inputElement = document.getElementById("inputElement")
                let param = new FormData(); // 创建form对象
                param.append("category", this.info.category);
                param.append("name", this.info.name);
                param.append("path", this.info.path);
                param.append("time", this.info.date + " " + this.info.time);
                param.append("user_name", this.$root.user_name);
                param.append("contact_info", this.info.contact_info);
                param.append("type", this.info.type);
                param.append("if_find", this.info.if_find);
                if(inputElement.files.length>0){
                  let file = inputElement.files[0];
                  param.append("picture", file);
                }
                let config = {
                  headers: {
                    "Content-Type": "multipart/form-data"
                  }
                };
                this.$axios.post("http://landx.top:9002/uploadInfo", param, config)
                  .then(response => {
                    console.log(response.data.msg);
                    if(response.data.msg=="SUCCESS"){
                      this.$alert('上传成功！', '提示', {
                  confirmButtonText: '确定'
                })
                    }
                  });
              } else {
                this.$alert('请先登陆！', '提示', {
                  confirmButtonText: '确定'
                })
              }
            } else {
              this.$alert('请输入拾取地点或路径！', '提示', {
                confirmButtonText: '确定'
              })
            }
          } else {
            this.$alert('请输入相关信息！', '提示', {
              confirmButtonText: '确定'
            })
            return false;
          }
        })
      },
      back() {
        this.$router.go(-1);
      },
      handleMessage(event) {
        const data = event.data;
        this.info.path.length = 0;
        for (var i in data.params.res) {
          var item = data.params.res[i];
          var tmp = [];
          tmp.push(item[0]);
          tmp.push(item[1]);
          this.info.path.push(tmp);
        }
        console.log(this.info.path);
      },
      getSTime(val) {
        this.sTime = val; //这个sTime是在data中声明的，也就是v-model绑定的值
      },
      upload() {
        let inputElement = document.getElementById("inputElement")
        let file = inputElement.files[0];

        let param = new FormData(); // 创建form对象
        param.append("file", file); // 通过append向form对象添加数据
        console.log(param);
      }
    }
  }
</script>

<style>
  .add_box {
    width: 1000px;
    margin: auto;
    border: 1px solid #DCDFE6;
    border-radius: 10px;
    box-shadow: 0 0 30px #DCDFE6;
  }

  .add_button {
    text-align: center;
  }
</style>



