import Vue from 'vue'
import Router from 'vue-router'
import Login from '../components/Login'
import Map from '../components/Map'
import lostinfo from '../components/lostinfo'
import findinfo from '../components/findinfo'
import search from '../components/search'
import search_info from '../components/searchInfo'
import addinfo from '../components/addinfo'
import user from '../components/user'
import newuser from '../components/newuser'

Vue.use(Router)

export default new Router({
  routes: [
	  {
		  path:'/',
		  name:'Login',
		  component:Login
	  },
    {
      path:'/map',
      name: 'map',
      component:Map
    },
    {
        path:'/lostinfo',
        name:'lostinfo',
        component:lostinfo
    },
    {
      path:'/findinfo',
      name:'findinfo',
      component:findinfo
    },
    {
      path:'/search',
      name:'search',
      component:search
    },
    {
      path:'/search_info',
      name:'search_info',
      component:search_info
    },
    {
      path:'/addinfo',
      name:'addinfo',
      component:addinfo
    },
    {
      path:'/user',
      name:'user',
      component:user
    },
    {
      path:'/newuser',
      name:'newuser',
      component:newuser
    }
  ]
})



