<template>
  <div>
   <div v-for="(lost_info,index) in lost_infos" :key="index" class='box'>
     <el-card>
       <div>
         <div style="float: right; ">
           <img v-bind:src=lost_info.picture style="max-width: 100px;height: auto;" />
         </div>
         <div style="float: left;">
           <h4>{{lost_info.user_name}}</h4>
           <p><span>{{lost_info.category}} | </span><span class="no-bd">{{lost_info.name}}</span>
           <p>拾取时间：{{lost_info.time}}</p>
           <p>联系方式：{{lost_info.contact_info}}</p>
           <p>描述：{{lost_info.discrip}}</p>
         </div>
       </div>
     </el-card>
   </div>
  </div>
</template>

<script>
  export default{
    name:"lostinfo",
    data(){
      return{
        lost_infos:[{
         id: '',
         name: '',
         discrip: '',
         category: '',
         path: [],
         time: '',
         if_find:'',
         user_name: '',
         contact_info: '',
         picture: '',
         type:''
        }]
      }
    },
    mounted(){
      this.$axios.get('http://landx.top:9002/lostInfo').then(
      response=>{
        this.lost_infos=response.data.points;
        for(var i in this.lost_infos){
          if(this.lost_infos[i].picture==null){
            this.lost_infos[i].picture='../assets/' + this.lost_infos[i].category + '.png';
            console.log(this.lost_infos[i].picture);
          }
        }
      }
      )
    }
  }
</script>

<style>
    .clearfix:before,
    .clearfix:after {
      display: table;
      content: "";
    }
    .clearfix:after {
      clear: both
    }
    .box-card {
      width: 600px;
      background-color: beige;
    }
    .box{
      width: 400px;
      margin:auto;
      padding: 10px;
      border: 1px solid #DCDFE6;
      border-radius: 10px;
      box-shadow: 0 0 30px #DCDFE6;
    }
</style>



