<template>
  <div>
    <div class="buttons">
      <center>
        <el-button round @click="show_findinfo">失物招领事件</el-button>
        <el-button round @click="show_lostinfo">遗失物事件</el-button>
        <router-link to="/search">
          <el-button round icon="el-icon-search">搜索</el-button>
        </router-link>
      </center>
      <div id="button">
        <router-link to="/addinfo">
          <el-button circle style="font-size: 20px;float: left;"><i class="el-icon-plus"></i></el-button>
        </router-link>
      </div>
      <div style="height: 60px;"></div>
      <div id="button">
        <router-link to="/user">
          <el-button circle style="font-size: 20px;float: left;"><i class="el-icon-user"></i></el-button>
        </router-link>
      </div>
    </div>
    <div id="container"></div>
    <transition name="fade">
      <iframe v-bind:src="url" v-if="info_hide_sign" width="400px" height="100%" class="info"
        frameborder="0"></iframe>
    </transition>
  </div>
</template>

<script>
  import {
    EventBus
  } from '../util/event-bus.js'
  export default {
    name: 'Map',
    mounted() {
      this.$root.map = new BMap.Map("container", {
        minZoom: 16,
        maxZoom: 19,
        poiIcon: false
      }); // 创建Map实例
      this.$root.map.centerAndZoom(new BMap.Point(116.316702, 39.998516), 17); // 初始化地图,设置中心点坐标和地图级别
      //添加地图类型控件
      this.$root.map.addControl(new BMap.MapTypeControl({
        mapTypes: [BMAP_NORMAL_MAP]
      }));
      this.$root.map.setCurrentCity("北京"); // 设置地图显示的城市 此项是必须设置的
      this.$root.map.enableScrollWheelZoom(true); //开启鼠标滚轮缩放
      this.$root.map.setMapStyleV2({
        styleId: '16334999f40c2739dc47c4786ac057b7'
      });
      this.$axios.get('http://landx.top:9002/findInfo').then(
        response => {
          var items = response.data.data;
          for (var i in items) {
            var item = items[i];
            var myIcon = new BMap.Icon(require('../assets/' + item.category + '.png'), new BMap.Size(28, 28));
            var pt = new BMap.Point(item.path[0][0], item.path[0][1]);
            var infoWindow = new BMap.InfoWindow("hh", pt);
            var marker = new BMap.Marker(pt, {
              icon: myIcon
            });
            this.$root.map.addOverlay(marker);
          }
        }
      );

    },
    data() {
      return {
        url: "",
        info_hide_sign: false,
        show_info_status: 0,
        user_name: "",
        from: "",
        map: ""
      }
    },
    methods: {
      show_lostinfo() {
        if (this.show_info_status == 1) {
          this.show_info_status = 0;
          this.info_hide_sign = false;
          this.url = "";
        } else {
          this.show_info_status = 1;
          this.info_hide_sign = true;
          this.url = window.location.origin + "/#/lostinfo";
        }
      },
      show_findinfo() {
        if (this.show_info_status == 2) {
          this.show_info_status = 0;
          this.info_hide_sign = false;
          this.url = "";
        } else {
          this.show_info_status = 2;
          this.info_hide_sign = true;
          this.url = window.location.origin + "/#/findinfo";
        }
      },

    }
  }
</script>

<style>
  #container {
    position: absolute;
    overflow: hidden;
    width: 99%;
    height: 99%;
    font-family: "微软雅黑";
  }

  .buttons {
    z-index: 1;
    position: fixed;
    margin: auto;
    width: 100%;
  }

  .info {
    z-index: 1;
    position: fixed;
    margin: auto;
    width: 450px;
  }

  #button {
    margin-left: auto;
    float: right;
    width: 60px;
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>



