<template>
  <div>
    <div style="text-align:center">
      <p style="height: 20px;"></p>
      <i class="el-icon-s-custom" style="font-size: 40px;"></i>
      <p style="font-size: 20px;">{{this.$root.user_name}}</p>
      <div style="text-align:center">
        <center>
          <el-container style="width: 1000px; border: 1px solid #eee">
            <el-main style="background-color: rgb(238, 241, 246)">
              <h4>失物招领事件</h4>
              <div v-for="(find_info,index) in find_infos" :key="index" class='box'>
                <el-card>
                  <div>
                    <div style="float: right; ">
                      <img v-bind:src=find_info.picture style="max-width: 100px;height: auto;" />
                    </div>
                    <div style="float: left;text-align: left;">
                      <h4>{{find_info.user_name}}</h4>
                      <p><span>{{find_info.category}} | </span><span class="no-bd">{{find_info.name}}</span>
                      <p>拾取时间：{{find_info.time}}</p>
                      <p>联系方式：{{find_info.contact_info}}</p>
                      <p>描述：{{find_info.discrip}}</p>
                      <p>是否找到：{{find_info.show_find}}
                        <el-button round @click="change_find(index)" type="primary">改变寻找状态</el-button>
                      </p>
                    </div>
                  </div>
                </el-card>
              </div>
            </el-main>
            <el-main>
              <h4>遗失物事件</h4>
              <div v-for="(lost_info,index) in lost_infos" :key="index" class='box'>
                <el-card>
                  <div>
                    <div style="float: right; ">
                      <img v-bind:src=lost_info.picture style="max-width: 100px;height: auto;" />
                    </div>
                    <div style="float: left;text-align: left;">
                      <h4>{{lost_info.user_name}}</h4>
                      <p><span>{{lost_info.category}} | </span><span class="no-bd">{{lost_info.name}}</span>
                      <p>拾取时间：{{lost_info.time}}</p>
                      <p>联系方式：{{lost_info.contact_info}}</p>
                      <p>描述：{{lost_info.discrip}}</p>
                      <p>是否找到：{{lost_info.show_find}}
                        <el-button round @click="change_lost(index)" type="primary">改变寻找状态</el-button>
                      </p>
                    </div>
                  </div>
                </el-card>
              </div>
            </el-main>
          </el-container>
        </center>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "user",
    data() {
      return {
        find_infos: [{
          id: '',
          name: '',
          discrip: '',
          category: '',
          path: [],
          time: '',
          if_find: '',
          user_name: '',
          contact_info: '',
          picture: '',
          type: '',
          show_find:''
        }],
        lost_infos: [{
          id: '',
          name: '',
          discrip: '',
          category: '',
          path: [],
          time: '',
          if_find:'',
          user_name: '',
          contact_info: '',
          picture: '',
          type: '',
          show_find:''
        }],
      }
    },
    mounted() {
      if(sessionStorage.getItem('hasEnter')=='1'){
        this.$root.user_name=sessionStorage.getItem('user_name');
        this.$root.token=sessionStorage.getItem('token');
        this.$root.hasEnter=true;
      }
      if (this.$root.hasEnter) {
        this.$axios.post('http://landx.top:9002/searchInfo', {
            user_name: this.$root.user_name,
            type: '1'
          })
          .then(response => {
            if (response.data.msg == "SUCCESS") {
              this.find_infos = response.data.data;
              for (var i in this.find_infos) {
                if (this.find_infos[i].if_find == '1') {
                  this.find_infos[i].show_find = '是';
                } else {
                  this.find_infos[i].show_find = '否';
                }
              }
            }
          });
        this.$axios.post('http://landx.top:9002/searchInfo', {
            user_name: this.$root.user_name,
            type: '0'
          })
          .then(response => {
            if (response.data.msg == "SUCCESS") {
              this.lost_infos = response.data.data;
              for (var i in this.lost_infos) {
                if (this.lost_infos[i].if_find == '1') {
                  this.lost_infos[i].show_find = '是';
                } else {
                  this.lost_infos[i].show_find = '否';
                }
              }
            }
          });
      } else {
        this.$router.push('/');
      }
    },
    methods: {
      change_find(index) {
        this.$axios.post('http://landx.top:9002/updateIfFind', {
          if_find:1-this.find_infos[index].if_find,
            solver: this.$root.user_name,
            id: this.find_infos[index].id
          })
          .then(response => {
            console.log(response.data.msg);
            if (response.data.msg == "SUCCESS") {
              if(this.find_infos[index].show_find=='是'){
                this.find_infos[index].show_find='否'
              }
              else{
                this.find_infos[index].show_find='是'
              }
              this.$router.go(0);
            } else {
              this.$alert('系统开小差啦~', '提示', {
                confirmButtonText: '确定'
              });
            }
          })
      },
      change_lost(index) {
        console.log(this.lost_infos[index].if_find);
        this.$axios.post('http://landx.top:9002/updateIfFind', {
          if_find:1-this.lost_infos[index].if_find,
            solver: this.$root.user_name,
            id: this.lost_infos[index].id
          })
          .then(response => {
            console.log(response.data.msg);
            if (response.data.msg == "SUCCESS") {
              if(this.lost_infos[index].show_find=='是'){
                this.lost_infos[index].show_find='否'
              }
              else{
                this.lost_infos[index].show_find='是'
              }
               this.$router.go(0);
            } else {
              this.$alert('系统开小差啦~', '提示', {
                confirmButtonText: '确定'
              });
            }
          })
      }
    }
  }
</script>

<style>
  .box {
    width: 400px;
    margin: auto;
    padding: 10px;
    border: 1px solid #DCDFE6;
    border-radius: 10px;
    box-shadow: 0 0 30px #DCDFE6;
  }
</style>



